import React from 'react'

function HistoryInfo() {
  return (
    <div className='historyinfo'>
       <div className='historyinfo__wrapper'>
          <h4 className='alt__title'>About Our Food</h4>
          <h3 className='title'>Delivering Awesome Since 1960</h3>
          <p className='description'>Time flies when you do what you love. We know because weve been making great pizza since 1967. Along the way, weve grown to more than 750 restaurants from coast to coast, becoming a trusted brand and leader in the foodservice industry. Were proud of the quality food we make  and the difference we make in communities across Canada. Weve made a lot of pies  and memories  over the years. Find out what goes into both!</p>
          <img src='https://demo2wpopal.b-cdn.net/poco/wp-content/uploads/2020/09/history_donh-poco.png' />
       </div>
    </div>
  )
}

export default HistoryInfo